* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: #bdc3c7 linear-gradient(to right, #2c3e50, #bdc3c7);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.container {
  max-width: 1300px;
  margin: auto;
  padding-top: 40px;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

ul, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.list {
  list-style-type: none;
}

#search-box {
  font: inherit;
  max-width: 500px;
  margin: 0 right auto;
  border: none;
  outline: none;
  margin-bottom: 20px;
  padding: 8px;
  font-size: 48px;
  display: block;
  box-shadow: 0 0 5px 5px rgba(189, 195, 199, .75);
}

.country-list, .country-info {
  max-width: 100%;
  flex-flow: column wrap;
  justify-content: center;
  row-gap: 15px;
  padding: 15px;
  display: flex;
}

.country-list-styles, .country-info-styles {
  background-color: #bdc3c7;
  border: 2px solid rgba(223, 221, 221, .5);
  border-radius: 5px;
  box-shadow: inset 0 0 5px 5px rgba(223, 221, 221, .75);
}

.country-item {
  background-color: #676869;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  padding: 10px;
  display: flex;
  box-shadow: inset 0 0 15px 5px rgba(223, 221, 221, .75);
}

.item-svg {
  border: 5px solid #f0f8ff;
  border-radius: 10px;
}

.item-title {
  color: #f0f8ff;
  font-size: 30px;
}

.title-wrap {
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 40px;
  display: flex;
}

.title {
  background-color: #2aa5a0;
  background-image: linear-gradient(90deg, #ba3740, #053937);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  font-family: Abril Fatface, cursive;
  font-size: 100px;
}

.info-wrap {
  flex-direction: row;
  gap: 30px;
  display: flex;
}

.sub-title, .text {
  font-size: 60px;
}

.post-title {
  color: #f0f8ff;
  margin-bottom: 10px;
  font-size: 30px;
}

